// src/ItemMarket.js - Firestore    (,  ,  )
import React, { useState, useEffect, useCallback, useMemo, useRef } from "react";
import { useAuth } from "../../contexts/AuthContext";
import { useItems } from "../../contexts/ItemContext";
import {
  db,
  addActivityLog,
  addTransaction,
} from "../../firebase";

import {
  collection,
  doc,
  runTransaction,
  increment,
  serverTimestamp,
  addDoc,
  updateDoc,
  deleteDoc,
  query,
  orderBy,
  where,
  getDoc,
  getDocs,
  limit,
  startAfter,
} from "firebase/firestore";

import "./ItemMarket.css";
import { formatKoreanCurrency } from '../../numberFormatter';

const formatDate = (date) => {
  if (!date) return '  ';
  if (date.toDate) return date.toDate().toLocaleDateString(); // Firestore Timestamp
  if (date.toLocaleDateString) return date.toLocaleDateString(); // JavaScript Date
  return '  ';
};

//   
const ProposalModal = ({ item, onSave, onCancel, currentUser }) => {
  const [proposalPrice, setProposalPrice] = useState("");
  const [proposalMessage, setProposalMessage] = useState("");
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleSave = async () => {
    if (isSubmitting) {
      return;
    }

    const price = parseInt(proposalPrice, 10);

    if (isNaN(price) || price <= 0) {
      alert("  .");
      return;
    }
    if (price > (currentUser?.cash || 0)) {
      alert("  .");
      return;
    }

    setIsSubmitting(true);

    const proposalData = {
      itemId: item.id,
      proposedPrice: price,
      message: proposalMessage.trim() || " ",
    };

    try {
      await onSave(proposalData);
    } catch (error) {
      alert("    .");
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div
      className="modal-overlay"
      onClick={onCancel}
      style={{
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: 'rgba(0, 0, 0, 0.5)',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        zIndex: 9999
      }}
    >
      <div
        className="modal-container"
        onClick={(e) => e.stopPropagation()}
        style={{
          backgroundColor: '#1a1a2e',
          borderRadius: '24px',
          padding: '20px',
          minWidth: '400px',
          maxWidth: '500px',
          maxHeight: '90vh',
          overflow: 'auto',
          border: '1px solid rgba(255, 255, 255, 0.1)',
          boxShadow: '0 20px 60px rgba(0, 0, 0, 0.6)',
          color: '#e2e8f0'
        }}
      >
        <div className="modal-header">
          <h3> </h3>
          <button className="close-button" onClick={onCancel}></button>
        </div>
        <div className="modal-content">
          <div className="proposal-item-info">
            <h4>{item.itemName}</h4>
            <p> : {(item.price || item.totalPrice || 0).toLocaleString()}</p>
            <p>: {item.sellerName}</p>
          </div>
          <div className="form-group">
            <label htmlFor="proposalPrice">  ()</label>
            <input
              type="number"
              id="proposalPrice"
              value={proposalPrice}
              onChange={(e) => {
                setProposalPrice(e.target.value);
              }}
              placeholder="  "
              min="1"
              max={currentUser?.cash || 0}
              disabled={isSubmitting}
            />
            <p className="form-hint">
               : {(currentUser?.cash || 0).toLocaleString()}
            </p>
          </div>
          <div className="form-group">
            <label htmlFor="proposalMessage"> ()</label>
            <textarea
              id="proposalMessage"
              value={proposalMessage}
              onChange={(e) => {
                setProposalMessage(e.target.value);
              }}
              placeholder="   "
              rows="3"
              disabled={isSubmitting}
            />
          </div>
        </div>
        <div className="modal-footer">
          <button onClick={() => {
            onCancel();
          }} className="modal-button cancel" disabled={isSubmitting}>
            
          </button>
          <button
            onClick={() => {
              handleSave();
            }}
            className="modal-button propose"
            disabled={isSubmitting || !proposalPrice}
          >
            {isSubmitting ? " ..." : ""}
          </button>
        </div>
      </div>
    </div>
  );
};

//    
const ItemRegistrationModal = ({ onSave, onCancel, userItems = [] }) => {
  const [registrationType, setRegistrationType] = useState("inventory"); // "inventory" or "custom"
  const [selectedInventoryItem, setSelectedInventoryItem] = useState("");
  const [selectedQuantity, setSelectedQuantity] = useState(1);
  const [itemName, setItemName] = useState("");
  const [description, setDescription] = useState("");
  const [price, setPrice] = useState("");
  const [category, setCategory] = useState("");
  const [isSubmitting, setIsSubmitting] = useState(false);

  //    
  const groupedInventoryItems = useMemo(() => {
    if (!userItems || userItems.length === 0) return [];

    const itemsMap = new Map();
    userItems.forEach(item => {
      if (!item || !item.itemId || typeof item.quantity !== 'number' || item.quantity <= 0) {
        return;
      }

      const key = item.itemId;
      if (itemsMap.has(key)) {
        const existingGroup = itemsMap.get(key);
        existingGroup.totalQuantity += item.quantity;
        existingGroup.sourceDocs.push(item);
      } else {
        itemsMap.set(key, {
          displayInfo: {
            itemId: item.itemId,
            name: item.name || '   ',
            icon: item.icon || '',
            description: item.description || '',
            type: item.type || 'general'
          },
          totalQuantity: item.quantity,
          sourceDocs: [item],
        });
      }
    });

    return Array.from(itemsMap.values()).sort((a, b) =>
      a.displayInfo.name.localeCompare(b.displayInfo.name)
    );
  }, [userItems]);

  //        
  useEffect(() => {
    if (registrationType === "inventory" && selectedInventoryItem) {
      const selectedGroup = groupedInventoryItems.find(group =>
        group.displayInfo.itemId === selectedInventoryItem
      );

      if (selectedGroup) {
        setItemName(selectedGroup.displayInfo.name);
        setDescription(selectedGroup.displayInfo.description);
        setCategory(selectedGroup.displayInfo.type);
        setSelectedQuantity(1);
      }
    }
  }, [selectedInventoryItem, registrationType, groupedInventoryItems]);

  const handleSave = async () => {
    if (isSubmitting) return;

    if (registrationType === "inventory") {
      if (!selectedInventoryItem) {
        alert("  .");
        return;
      }
      if (selectedQuantity <= 0) {
        alert("  .");
        return;
      }

      const selectedGroup = groupedInventoryItems.find(group =>
        group.displayInfo.itemId === selectedInventoryItem
      );

      if (!selectedGroup || selectedGroup.totalQuantity < selectedQuantity) {
        alert("  .");
        return;
      }
    } else {
      if (!itemName.trim()) {
        alert(" .");
        return;
      }
    }

    const numericPrice = parseInt(price, 10);
    if (isNaN(numericPrice) || numericPrice <= 0) {
      alert("  .");
      return;
    }

    setIsSubmitting(true);
    try {
      const itemData = {
        itemName: itemName.trim(),
        description: description.trim(),
        price: numericPrice,
        category,
        registrationType
      };

      if (registrationType === "inventory") {
        const selectedGroup = groupedInventoryItems.find(group =>
          group.displayInfo.itemId === selectedInventoryItem
        );

        //  inventory  ID  (   )
        itemData.inventoryItemId = selectedGroup.sourceDocs[0].id;
        itemData.quantity = selectedQuantity;
        itemData.sourceDocs = selectedGroup.sourceDocs;
        itemData.icon = selectedGroup.displayInfo.icon;
      }

      await onSave(itemData);
    } catch (error) {
      alert("    .");
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div
      className="modal-overlay"
      onClick={onCancel}
      style={{
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: 'rgba(0, 0, 0, 0.8)',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        zIndex: 9999,
        backdropFilter: 'blur(8px)'
      }}
    >
      <div
        className="modal-container"
        onClick={(e) => e.stopPropagation()}
        style={{
          backgroundColor: '#1a1a2e',
          borderRadius: '24px',
          padding: '20px',
          minWidth: '400px',
          maxWidth: '500px',
          maxHeight: '90vh',
          overflow: 'auto',
          border: '1px solid rgba(255, 255, 255, 0.1)',
          boxShadow: '0 20px 60px rgba(0, 0, 0, 0.6)',
          color: '#e2e8f0'
        }}
      >
        <div className="modal-header">
          <h3> </h3>
          <button className="close-button" onClick={onCancel}></button>
        </div>
        <div className="modal-content">
          <div className="form-group">
            <label> </label>
            <div className="radio-group">
              <label>
                <input
                  type="radio"
                  value="inventory"
                  checked={registrationType === "inventory"}
                  onChange={(e) => setRegistrationType(e.target.value)}
                  disabled={isSubmitting}
                />
                  
              </label>
              <label>
                <input
                  type="radio"
                  value="custom"
                  checked={registrationType === "custom"}
                  onChange={(e) => setRegistrationType(e.target.value)}
                  disabled={isSubmitting}
                />
                 
              </label>
            </div>
          </div>

          {registrationType === "inventory" && (
            <>
              <div className="form-group">
                <label htmlFor="inventoryItem">   *</label>
                <select
                  id="inventoryItem"
                  value={selectedInventoryItem}
                  onChange={(e) => setSelectedInventoryItem(e.target.value)}
                  disabled={isSubmitting}
                >
                  <option value="">--   --</option>
                  {groupedInventoryItems.map(group => (
                    <option key={group.displayInfo.itemId} value={group.displayInfo.itemId}>
                      {group.displayInfo.name} ({group.totalQuantity})
                    </option>
                  ))}
                </select>
              </div>

              {selectedInventoryItem && (
                <div className="form-group">
                  <label htmlFor="quantity">  *</label>
                  <input
                    type="number"
                    id="quantity"
                    value={selectedQuantity}
                    onChange={(e) => setSelectedQuantity(Math.max(1, parseInt(e.target.value) || 1))}
                    min="1"
                    max={groupedInventoryItems.find(g => g.displayInfo.itemId === selectedInventoryItem)?.totalQuantity || 1}
                    disabled={isSubmitting}
                  />
                  <p className="form-hint">
                     {groupedInventoryItems.find(g => g.displayInfo.itemId === selectedInventoryItem)?.totalQuantity || 0}  
                  </p>
                </div>
              )}
            </>
          )}

          <div className="form-group">
            <label htmlFor="itemName"> *</label>
            <input
              type="text"
              id="itemName"
              value={itemName}
              onChange={(e) => setItemName(e.target.value)}
              placeholder=" "
              maxLength="50"
              disabled={isSubmitting || (registrationType === "inventory" && selectedInventoryItem)}
            />
          </div>
          <div className="form-group">
            <label htmlFor="description"> </label>
            <textarea
              id="description"
              value={description}
              onChange={(e) => setDescription(e.target.value)}
              placeholder="   "
              rows="3"
              maxLength="200"
              disabled={isSubmitting}
            />
          </div>
          <div className="form-group">
            <label htmlFor="price"> () *</label>
            <input
              type="number"
              id="price"
              value={price}
              onChange={(e) => setPrice(e.target.value)}
              placeholder=" "
              min="1"
              disabled={isSubmitting}
            />
          </div>
          <div className="form-group">
            <label htmlFor="category"></label>
            <select
              id="category"
              value={category}
              onChange={(e) => setCategory(e.target.value)}
              disabled={isSubmitting || (registrationType === "inventory" && selectedInventoryItem)}
            >
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
            </select>
          </div>
        </div>
        <div className="modal-footer">
          <button onClick={onCancel} className="modal-button cancel" disabled={isSubmitting}>
            
          </button>
          <button
            onClick={handleSave}
            className="modal-button save"
            disabled={isSubmitting || !itemName.trim() || !price}
          >
            {isSubmitting ? " ..." : ""}
          </button>
        </div>
      </div>
    </div>
  );
};

//  ItemMarket 
const ItemMarket = () => {
  const auth = useAuth();
  const itemContext = useItems();
  const currentUser = auth.userDoc;
  const currentUserId = currentUser?.id;
  const classCode = currentUser?.classCode;

  const {
    marketListings: items, //    
    marketOffers: proposals, //    
    loading: contextLoading,
    refreshData,
    buyMarketItem,
    makeOffer,
    respondToOffer,
    listItemForSale,
    cancelSale,
    adminDeleteItem,
    userItems
  } = useItems();

  const { classmates, loading: authLoading } = useAuth();

  const [showRegistrationModal, setShowRegistrationModal] = useState(false);
  const [showProposalModal, setShowProposalModal] = useState(false);
  const [selectedItem, setSelectedItem] = useState(null);
  const [activeTab, setActiveTab] = useState("market");
  const [selectedCategory, setSelectedCategory] = useState("");
  const [searchTerm, setSearchTerm] = useState("");
  const [isPurchasing, setIsPurchasing] = useState({});

  //  []     
  const [marketSummary, setMarketSummary] = useState(null);
  const [summaryLoading, setSummaryLoading] = useState(true);
  const [lastUpdated, setLastUpdated] = useState(null);

  // :   (  )
  const [visibleItemsCount, setVisibleItemsCount] = useState(20);

  const loading = contextLoading || authLoading;

  //  []    (    90% )
  useEffect(() => {
    if (!classCode) {
      setSummaryLoading(false);
      return;
    }

    //           
    // ItemContext  marketListings     
    setSummaryLoading(false);
    setMarketSummary({});
    setLastUpdated(null);
  }, [classCode]);

  //      (  )
  useEffect(() => {
    return () => {
    };
  }, []);

  //     (classmates )
  const getUserNameById = useCallback((userId) => {
    if (!userId) return "  ";
    const user = classmates.find(u => u.id === userId || u.uid === userId);
    return user?.name || user?.displayName || "  ";
  }, [classmates]);

  //     (sellerName   )
  const getSellerName = useCallback((item) => {
    return item?.sellerName || getUserNameById(item?.sellerId);
  }, [getUserNameById]);

  //    (   )
  const handleItemRegistration = async (itemData) => {
    if (!currentUserId || !classCode) {
      alert(" .");
      return;
    }

    try {
      if (itemData.registrationType === "inventory") {
        const { inventoryItemId, quantity, price } = itemData;

        const result = await listItemForSale({ itemId: inventoryItemId, quantity, price });

        if (!result.success) throw new Error(result.message);

        alert(`${itemData.itemName} ${quantity}   .`);
      } else {
        //      (  )
        alert("      .");
        return;
      }

      setShowRegistrationModal(false);
      refreshData(); //   

    } catch (error) {
      alert("    : " + error.message);
    }
  };

  //   (:    )
  const handleProposal = async (proposalData) => {
    if (!currentUserId || !classCode) {
      alert(" .");
      return;
    }

    try {
      const result = await makeOffer(proposalData);
      if (!result.success) {
        throw new Error(result.message || " .");
      }
      alert("  .");
      setShowProposalModal(false);
      setSelectedItem(null);
    } catch (error) {
      alert("    : " + error.message);
    }
  };

  //    (:      )
  const handleDirectPurchase = async (item) => {
    if (isPurchasing[item.id]) return; // Prevent double-clicks

    if (!currentUserId) {
      alert(" .");
      return;
    }
    if (item.sellerId === currentUserId) {
      alert("     .");
      return;
    }

    const itemPrice = item.price || item.totalPrice || 0;
    if ((currentUser?.cash || 0) < itemPrice) {
      alert(` .`);
      return;
    }

    if (!window.confirm(`${item.itemName}() ${itemPrice.toLocaleString()} ?`)) {
      return;
    }

    setIsPurchasing(prev => ({ ...prev, [item.id]: true })); // Set purchasing state

    try {
      const result = await buyMarketItem(item.id);
      if (!result.success) {
        throw new Error(result.message || " .");
      }
      alert(" !");
      refreshData(); //  
    } catch (error) {
      if (error.message.includes("409")) { // Check for 409 conflict
        alert("     .");
      } else {
        alert("   : " + error.message);
      }
      refreshData(); //       
    } finally {
      setIsPurchasing(prev => ({ ...prev, [item.id]: false })); // Reset purchasing state
    }
  };

  //    (:    )
  const handleAcceptProposal = async (proposalId) => {
    if (!currentUserId || !classCode) return;

    const proposal = proposals.find(p => p.id === proposalId);
    if (!proposal) {
      alert("   .");
      return;
    }

    if (proposal.sellerId !== currentUserId) {
      alert("      .");
      return;
    }

    if (proposal.status !== "pending") {
      alert("  .");
      return;
    }

    const item = items.find(i => i.id === proposal.itemId);
    if (!item || item.status !== "available") {
      alert("     .");
      return;
    }

    if (!window.confirm(`${proposal.buyerName} ${proposal.proposedPrice.toLocaleString()}  ?`)) {
      return;
    }

    try {
      await runTransaction(db, async (transaction) => {
        const buyerRef = doc(db, "users", proposal.buyerId);
        const buyerSnap = await transaction.get(buyerRef);

        if (!buyerSnap.exists()) {
          throw new Error("    .");
        }

        const buyerData = buyerSnap.data();
        const buyerBalance = buyerData.cash || 0;

        if (buyerBalance < proposal.proposedPrice) {
          throw new Error("  .");
        }

        const proposalRef = doc(db, "classes", classCode, "marketProposals", proposalId);
        transaction.update(proposalRef, {
          status: "accepted",
          acceptedAt: serverTimestamp(),
        });

        const itemRef = doc(db, "classes", classCode, "marketItems", proposal.itemId);
        transaction.update(itemRef, {
          status: "sold",
          soldTo: proposal.buyerId,
          soldPrice: proposal.proposedPrice,
          soldAt: serverTimestamp(),
        });

        const sellerRef = doc(db, "users", proposal.sellerId);

        transaction.update(buyerRef, {
          cash: increment(-proposal.proposedPrice),
          updatedAt: serverTimestamp(),
        });

        transaction.update(sellerRef, {
          cash: increment(proposal.proposedPrice),
          updatedAt: serverTimestamp(),
        });
      });

      //       
      await refreshData();

      await Promise.all([
        addActivityLog(proposal.buyerId, " ",
          `${proposal.itemName}() ${proposal.proposedPrice.toLocaleString()} .`),
        addActivityLog(proposal.sellerId, " ",
          `${proposal.itemName}() ${proposal.proposedPrice.toLocaleString()} .`),
        addTransaction(proposal.buyerId, -proposal.proposedPrice,
          ` : ${proposal.itemName}`),
        addTransaction(proposal.sellerId, proposal.proposedPrice,
          ` : ${proposal.itemName}`)
      ]);

      alert("   .");
    } catch (error) {
      alert("    : " + error.message);
    }
  };

  //    (:    )
  const handleRejectProposal = async (proposalId) => {
    if (!currentUserId || !classCode) return;

    const proposal = proposals.find(p => p.id === proposalId);
    if (!proposal) {
      alert("   .");
      return;
    }

    if (proposal.sellerId !== currentUserId) {
      alert("      .");
      return;
    }

    if (proposal.status !== "pending") {
      alert("  .");
      return;
    }

    if (!window.confirm(`${proposal.buyerName}  ?`)) {
      return;
    }

    try {
      const proposalRef = doc(db, "classes", classCode, "marketProposals", proposalId);
      await updateDoc(proposalRef, {
        status: "rejected",
        rejectedAt: serverTimestamp(),
      });

      //       
      await refreshData();

      alert(" .");
    } catch (error) {
      alert("    : " + error.message);
    }
  };

  const handleCancelSale = async (itemId) => {
    const item = items.find(i => i.id === itemId);
    if (!item) return;

    if (item.sellerId !== currentUserId) {
      alert("     .");
      return;
    }

    const pendingProposals = proposals.filter(p =>
      p.itemId === itemId && p.status === "pending"
    );

    let confirmationText = "    ?";
    if (pendingProposals.length > 0) {
      confirmationText = `  ${pendingProposals.length}    .   ?   .`;
    }

    if (!window.confirm(confirmationText)) {
      return;
    }

    try {
      const result = await cancelSale(itemId); //   
      if (!result.success) {
        throw new Error(result.message || "  .");
      }
      alert(" .   .");
    } catch (error) {
      alert(`   : ${error.message}`);
    }
  };

  const handleAdminDeleteItem = async (listingId) => {
    if (!auth.isAdmin()) {
      alert("  .");
      return;
    }

    if (!window.confirm("    ?     .")) {
      return;
    }

    try {
      const result = await adminDeleteItem(listingId);
      if (!result.success) {
        throw new Error(result.message || " .");
      }
      alert("  .");
      refreshData();
    } catch (error) {
      alert("   : " + error.message);
      refreshData();
    }
  };



  const filteredItems = useMemo(() => {
    const filtered = (items || []).filter(item => {
      const matchesCategory = selectedCategory === "" || item.category === selectedCategory || item.itemType === selectedCategory;
      const matchesSearch = !searchTerm ||
        item.itemName.toLowerCase().includes(searchTerm.toLowerCase()) ||
        (item.description || '').toLowerCase().includes(searchTerm.toLowerCase()) ||
        (item.sellerName || '').toLowerCase().includes(searchTerm.toLowerCase());
      const isAvailable = item.status === "available" || item.status === "active";

      return matchesCategory && matchesSearch && isAvailable;
    });

    return filtered;
  }, [items, selectedCategory, searchTerm, currentUserId]);

  //    ()
  const myItems = useMemo(() => {
    return (items || []).filter(item => item.sellerId === currentUserId);
  }, [items, currentUserId]);

  // /   ()
  const { receivedProposals, sentProposals } = useMemo(() => {
    const received = (proposals || []).filter(proposal => proposal.sellerId === currentUserId);
    const sent = (proposals || []).filter(proposal => proposal.buyerId === currentUserId);
    return { receivedProposals: received, sentProposals: sent };
  }, [proposals, currentUserId]);

  //    ()
  const sortedMarketData = useMemo(() => {
    return marketSummary
      ? Object.entries(marketSummary).sort(([nameA], [nameB]) => nameA.localeCompare(nameB))
      : [];
  }, [marketSummary]);

  if (loading) {
    return <div className="market-container loading"> ...</div>;
  }
  if (!currentUser) {
    return <div className="market-container loading"> .</div>;
  }
  if (!classCode) {
    return <div className="market-container loading">   .</div>;
  }

  const visibleItems = filteredItems.slice(0, visibleItemsCount);

  return (
    <div className="market-container">
      <div className="market-header">
        <h1>  (: {classCode})</h1>
        <div className="header-info">
          <span> : {(currentUser.cash || 0).toLocaleString()}</span>
        </div>
      </div>

      <div className="market-tabs">
        <button
          className={`tab-button ${activeTab === "market" ? "active" : ""}`}
          onClick={() => setActiveTab("market")}
        >
            ({filteredItems.length})
        </button>
        <button
          className={`tab-button ${activeTab === "my-items" ? "active" : ""}`}
          onClick={() => setActiveTab("my-items")}
        >
            ({myItems.length})
        </button>
        <button
          className={`tab-button ${activeTab === "proposals" ? "active" : ""}`}
          onClick={() => setActiveTab("proposals")}
        >
            ({receivedProposals.filter(p => p.status === "pending").length})
        </button>
      </div>

      <div className="market-content">

        {activeTab === "market" && (
          <div className="market-section">
            <div className="market-controls">
              <div className="search-and-filter">
                <input
                  type="text"
                  placeholder=", ,  ..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="search-input"
                />
                <select
                  value={selectedCategory}
                  onChange={(e) => setSelectedCategory(e.target.value)}
                  className="category-filter"
                >
                  <option value=""> </option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
              </div>
              <button
                onClick={() => setShowRegistrationModal(true)}
                className="register-button"
              >
                 
              </button>
            </div>

            {loading ? (
              <div className="loading">  ...</div>
            ) : visibleItems.length === 0 ? (
              <div className="empty-state" style={{ backgroundColor: '#1a1a2e', color: '#94a3b8' }}>
                {searchTerm || selectedCategory !== ""
                  ? "    ."
                  : "  ."}
              </div>
            ) : (
              <>
                <div className="items-grid">
                  {visibleItems.map(item => (
                    <div key={item.id} className="item-card">
                      <div className="item-info">
                        {/*     : name, itemName   */}
                        <h3>{item.name || item.itemName || "  "}</h3>
                        <p className="item-description">{item.description || " "}</p>
                        <p className="item-price">{(item.price || item.totalPrice || 0).toLocaleString()}</p>
                        <p className="item-seller">: {getSellerName(item)}</p>
                        <p className="item-category">{item.category || item.itemType || item.type || ""}</p>
                      </div>
                      <div className="item-actions">
                        {item.sellerId !== currentUserId && (
                          <>
                            <button
                              onClick={() => handleDirectPurchase(item)}
                              className="buy-button"
                              disabled={isPurchasing[item.id] || !currentUser || (currentUser.cash || 0) < (item.price || item.totalPrice || 0)}
                            >
                              {isPurchasing[item.id] ? " ..." : " "}
                            </button>
                            <button
                              onClick={() => {
                                setSelectedItem(item);
                                setShowProposalModal(true);
                              }}
                              className="propose-button"
                            >
                              
                            </button>
                          </>
                        )}
                        {auth.isAdmin() && (
                          <button
                            onClick={() => handleAdminDeleteItem(item.id)}
                            className="delete-button"
                          >
                             
                          </button>
                        )}
                      </div>
                    </div>
                  ))}
                </div>

                {filteredItems.length > visibleItemsCount && (
                  <div className="load-more-container">
                    <button
                      onClick={() => setVisibleItemsCount(prev => prev + 20)}
                      className="load-more-button"
                    >
                       
                    </button>
                  </div>
                )}
              </>
            )}
          </div>
        )}

        {activeTab === "my-items" && (
          <div className="my-items-section">
            <div className="section-header">
              <h2> </h2>
              <button
                onClick={() => setShowRegistrationModal(true)}
                className="register-button"
              >
                 
              </button>
            </div>
            {myItems.length === 0 ? (
              <div className="empty-state" style={{ backgroundColor: '#1a1a2e', color: '#94a3b8' }}>  .</div>
            ) : (
              <div className="items-list">
                {myItems.map(item => (
                  <div key={item.id} className="item-row">
                    <div className="item-info">
                      <h3>{item.itemName}</h3>
                      <p>{item.description}</p>
                      <p className="price">{(item.price || item.totalPrice || 0).toLocaleString()}</p>
                      <p>: {item.category || item.itemType || ""}</p>
                      <span className={`status ${item.status}`}>
                        {item.status === "available" || item.status === "active" ? "" :
                          item.status === "sold" ? ` (${(item.soldPrice || item.totalPrice || 0).toLocaleString()})` : ""}
                      </span>
                      {item.isLegacy && <span className="legacy-badge"> </span>}
                      {item.soldAt && (
                        <p>: {formatDate(item.soldAt)}</p>
                      )}
                    </div>
                    <div className="item-actions">
                      {(item.status === "available" || item.status === "active") && (
                        <button
                          onClick={() => handleCancelSale(item.id)}
                          className="delete-button"
                        >
                          
                        </button>
                      )}
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>
        )}

        {activeTab === "proposals" && (
          <div className="proposals-section">
            <div className="section-header">
              <h2> </h2>
            </div>

            <div className="proposals-tabs">
              <h3>  ({receivedProposals.length})</h3>
              {receivedProposals.length === 0 ? (
                <div className="empty-state" style={{ backgroundColor: '#1a1a2e', color: '#94a3b8' }}>  .</div>
              ) : (
                <div className="proposals-list">
                  {receivedProposals.map(proposal => (
                    <div key={proposal.id} className="proposal-card">
                      <div className="proposal-info">
                        <h4>{proposal.itemName}</h4>
                        <p>: {getUserNameById(proposal.buyerId)}</p>
                        <p> : <strong>{proposal.proposedPrice.toLocaleString()}</strong></p>
                        <p> : {proposal.originalPrice.toLocaleString()}</p>
                        {proposal.message && <p>: "{proposal.message}"</p>}
                        <p>: {formatDate(proposal.createdAt)}</p>
                        <p>: <span className={`status ${proposal.status}`}>
                          {proposal.status === "pending" ? "" :
                            proposal.status === "accepted" ? "" : ""}
                        </span></p>
                      </div>
                      {proposal.status === "pending" && (
                        <div className="proposal-actions">
                          <button
                            onClick={() => handleAcceptProposal(proposal.id)}
                            className="accept-button"
                          >
                            
                          </button>
                          <button
                            onClick={() => handleRejectProposal(proposal.id)}
                            className="reject-button"
                          >
                            
                          </button>
                        </div>
                      )}
                    </div>
                  ))}
                </div>
              )}

              <h3>  ({sentProposals.length})</h3>
              {sentProposals.length === 0 ? (
                <div className="empty-state" style={{ backgroundColor: '#1a1a2e', color: '#94a3b8' }}>  .</div>
              ) : (
                <div className="proposals-list">
                  {sentProposals.map(proposal => (
                    <div key={proposal.id} className="proposal-card">
                      <div className="proposal-info">
                        <h4>{proposal.itemName || '  '}</h4>
                        <p>: {getUserNameById(proposal.sellerId)}</p>
                        <p> : <strong>{(proposal.proposedPrice || 0).toLocaleString()}</strong></p>
                        <p> : {(proposal.originalPrice || 0).toLocaleString()}</p>
                        {proposal.message && <p>: "{proposal.message}"</p>}
                        <p>: {formatDate(proposal.createdAt)}</p>
                        <p>: <span className={`status ${proposal.status}`}>
                          {proposal.status === "pending" ? "" :
                            proposal.status === "accepted" ? "" : ""}
                        </span></p>
                        {proposal.acceptedAt && (
                          <p>: {formatDate(proposal.acceptedAt)}</p>
                        )}
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </div>
        )}
      </div>

      {showRegistrationModal && (
        <ItemRegistrationModal
          onSave={handleItemRegistration}
          onCancel={() => setShowRegistrationModal(false)}
          userItems={itemContext.userItems || []}
        />
      )}

      {showProposalModal && selectedItem && (
        <ProposalModal
          item={selectedItem}
          onSave={handleProposal}
          onCancel={() => {
            setShowProposalModal(false);
            setSelectedItem(null);
          }}
          currentUser={currentUser}
        />
      )}
    </div>
  );
};

export default ItemMarket;